.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.videListItem {
  cursor: pointer;
}

.videListItem:hover {
  background-color: rgba(253, 253, 253, 0.8);
}

.search-bar {
  width: 60%;
  margin: 20px auto 20px auto;
  text-align: left;
  border: 1px solid rgba(139, 139, 138, 0.80);
  border-radius: 3px;
  padding-top: 5px;
  padding-bottom: 5px;
}

.search-bar input {
  padding-left: 10px;
  width: 90%;
  outline: none;
  border: none;
}

.search-bar i {
  margin: 0;
  float: right;
}



